import Scroll from "../commion/scroll/Scroll";      //=> BetterScroll
import TopRocket from "../commion/BackTool/TopRocket";  //=> 
import Recommend from "../commion/Label/Recommend";     //=> 
import Chart from "../commion/Rotation/Chart";          //=> 
import  { Toast } from 'mint-ui';
import axios from 'axios';
import qs from 'qs';
export default {
  components:{
    Scroll,      //=> BetterScroll
    TopRocket,   //=> 
    Recommend,   //=> 
    Chart,       //=> 
  },
     data(){
         return{
           title1: "",
           title2: "",
           lunbotuList:[],
           isShowBackTop: false,      //=>
         };
     },
      created(){
        this.getLunbotu();
      },
      methods:{
         getLunbotu(){
             axios.get('./static/Snake.json').then(result => {
                 if (result.data.status === 0){
                     //
                     this.lunbotuList = result.data.message;
                   // Toast('.....')
                 }else{
                     //
                     Toast('.....');
                 }
             });
         },
        //=> 
        loadMore(){
          console.log('------');
          // this.$refs.scroll.finishPullUp()
          // this.$refs.scroll.refresh()
        },
        //=> 
        contentScroll(position){
          /*
          * 
          * position.yscrolly
          * y500this.backtoptrue
          * */
           this.isShowBackTop = (-position.y) > 500
        },
        /*
        * x,y,
        * x:0,y:60,itme:600
        * y60
        *  */
        backtopClick(){
           this.$refs.scroll.scrollTo(0,60,600)
        }
      },
  }



