import { Toast } from 'mint-ui';
  export default {
      data() {
          return {
              isLoading:false,
              keywords:'',
              list:[],
          };
      },
      created() {
          this.getNewsList();
          this.search();
      },
      methods: {
          onRefresh() {
              setTimeout(() => {
                  this.isLoading = false;
                  Toast("")
              }, 2000);
          },
          getNewsList(){
              //
              axios.get('./static/Snake.json',
                  {
                      'headers':{
                          'Content-Type':'Access-Control-Allow-Origin:*,Access-Control-Allow-Method:POST,GET,PUT,DELETE,OPTIONS,Snake/json',
                      }
                  }
              ).then(result => {
                  if (result.data.status === 0){
                      //data
                      this.list = result.data.clothing;
                  }else{
                      Toast('')
                  }
              })
          },
          //
          search(keywords){
              let newlist = [];
              //For each some filter findIndex
              //
              //
              // this.list.forEach(item => {
              // 	if(item.name.indexOf(keywords) != -1){
              // 	newlist.push(item)
              // 	}
              // })
              // return newlist
              //
              //ES6String.prototype.includes('')
              //truefalse
              // contain
              newlist = this.list.filter(item => {
                  if (item.category.includes(keywords)){
                      return item
                  }
              });
              return newlist
          }
      },
  }



