import BScroll from 'better-scroll'

  export default {
    name: "BetterScroll",
    props:{
      /**/
      probeType:{
        type:Number,
        default: 0,
      },
      pullUpLoad:{
        type: Boolean,
        default: false,
      }
    },
    data(){
      return{
        scroll: null,
      }
    },
    mounted(){
      setTimeout((item) => {
        this.$nextTick(() => {
          if (!this.scroll){
            //=> 1. scroll
            this.scroll = new BScroll(this.$refs.wrapper,{
              /*
              * click:false
              * better-scroll  click 
              *  truebetter-scroll  click  event  _constructed true
              * */
              click: true,
              /*
              * probeType123;
              * 1 scroll
              * 2 scroll
              * 3 scrollswipescroll*/
              probeType:this.probeType,
               /**/
              mouseWheel: true,
              /*pullUpLoadfalse true*/
              pullUpLoad: this.pullUpLoad,
            });
            console.log(this.scroll);
            //=> 2.
            this.scroll.on('scroll',(position) => {
              //=> 
              // this.scroll.refresh();
              console.log(this.scroll.maxScrollY);
              this.$emit('scroll',position)
            });
            //=> 3.
            this.scroll.on('pullingUp',() => {
              //=> 
              console.log('');
              this.$emit('pullingUp')
            })
          }
        })
      },50)
    },
    methods:{
      finishPullUp(){
        this.scroll.finishPullUp();
      },
      /*
      * 
      * x,y:
      * itme
      * */
      scrollTo(x,y,itme=500){
        this.scroll.scrollTo(x,y,itme)
      }
    }
  }



